body{font-family:'Rock Salt', cursive;color:#f4f4f4;font-size:13px;line-height:1.5;letter-spacing:8}body>h1{font-size:46px;text-align:center}#layout{display:flex}#img{z-index:-1}.button{background-color:aquamarine;padding:1em 2em;color:black;border:0}.button:hover{background-color:blue}.modal{display:none;position:fixed;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:rgba(0,0,0,0.5)}.modal-content{background-color:#f4f4f4;margin:20% auto;width:70%;box-shadow:0 5px 8px 0 rgba(0,0,0,0.3),0 7px 20px 0 #000;-webkit-animation-name:modalopen;animation-name:modalopen;-webkit-animation-duration:.5s;animation-duration:.5s}.close-btn{color:#ccc;float:right;font-size:30px}.close-btn:hover,.close-btn:focus{color:#000;text-decoration:none;cursor:pointer}@-webkit-keyframes modalopen{from{opacity:0}to{opacity:1}}@keyframes modalopen{from{opacity:0}to{opacity:1}}.modal-header{background:#546683;padding:15px;color:#fff}.modal-header h2,.modal-footer h3{margin:0}.modal-body{padding:10px 20px;color:#546683}.modal-footer{background:#546683;padding:10px;color:#fff;font-size:10px}#school-list-title{width:400px;font-size:22px}#school-list{max-height:600px;width:400px;overflow:auto;list-style:none;line-height:4}#school-name{font-size:8px}#container-one,#container-two{width:180px;vertical-align:middle;white-space:nowrap;position:relative}#container-one input#searchCurrent:focus,#container-two input#searchTarget:focus{outline-color:#546683}#container-one input#searchCurrent,#container-two input#searchTarget{width:150px;height:50px;background:#2b303b;border:none;font-size:10pt;float:left;color:#63717f;padding-left:45px;border-radius:5px}input[type=number]::-webkit-inner-spin-button,input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;-moz-appearance:none;appearance:none;margin:0}#container-one input#searchCurrent::-webkit-input-placeholder,#container-two input#searchTarget::-webkit-input-placeholder{color:#65737e}#container-one input#searchCurrent:-ms-input-placeholder,#container-two input#searchTarget:-ms-input-placeholder{color:#65737e}.icon1{position:absolute;margin-left:17px;margin-top:17px;z-index:1;color:#4f5b66}.icon2{position:absolute;margin-left:17px;margin-top:17px;z-index:1;color:#4f5b66}#container-one button,#container-two button{background-color:transparent;border:none}#container-one button:focus,#container-two button:focus{outline:none}.current:hover,.target:hover{color:#587eb9}.college{margin:20px}.college:hover{color:#587eb9}#left{display:flex}#title{text-align:center}.current,.target{font-size:9px;overflow:hidden}
